a, a:visited {
  text-decoration: none;
  position: relative;
  color: #4B6C66;
}
a:after, a:visited:after {
  content: '';
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  background: #4B6C66;
  transition: 0.2s;
}
a:hover:after, a:visited:hover:after {
  width: 100%;
}