.landingMain  {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.landingImage  {
    height: 38em;
    padding-top: 3em;
}

.landingHeader {
    font-family: 'Montserrat', sans-serif;
    color:  #E5847D;
    font-size: 65px;
    margin: 0;
    padding-left: 1em;
    padding-top: 2em;
}

.landingIntro {
    font-family: 'Source Sans Pro', sans-serif;
    color:  #E5847D;
    font-size: 30px;
    margin: 0;
    display: flex;
    flex-direction: column;
    padding-left: 2em;
    padding-top: 1em;
    padding-right: 1em;
}

.landingIntroContainer {
    display: flex;
    flex-direction: column;
}

.skillsImage {
    height: 100px;
}

.skillsContainer {
    display: flex;
    flex-direction: row;
    padding-top: 4em;
    padding-left: 1.7em;
}

.skillDetail {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.skillsDescriptionText {
    font-family: 'Montserrat', sans-serif;
    color:  #d6bc55;
    font-size: 18px;
}

.viewWork {
    font-family: 'Montserrat', sans-serif;
    font-weight:700;
    color:  #446f72;
    font-size: 22px;
}

.viewWorkText {
    padding-left: 1.7em;
}
